import React from 'react';
import about from './about.jpg'
import logobeige from './logobeige.png'


export function About(){
    return(
        <div style={{margin: "6%"}}>
            <div className="logodiv">
            <img src={logobeige} alt="Logo" width="130" />
            </div>
            <h2 style={{textAlign: "center", color:"#F5F3E3", marginTop:"5%"}}>Vlkommen till amig@s!</h2>
            <p style={{ marginBottom:"5%"}}>
            Letar du efter en trningskompis, en resepartner fr en helg eller bara en likasinnad fr intressanta samtal ver en trevlig middag? G med i vr community fr att trffa andra fr olika aktiviteter.
            Hr p amig@s uppmuntrar vi sociala interaktioner och vr vision r att om man inte vill vara ensam, ska man inte behva vara det! G med i amig@s idag utan en lng registreringsprocess, och kasta dig ut fr att hitta en amig@, kanske en vn fr livet!
            </p>
            <img src={about} alt="amigas" style={{height: "auto", width: "100%", margin: "auto"}} />
        </div>
        
    )
}